*{
    margin: 0;
    padding: 0;
}
nav{
    display: flex;
    align-items: centre;
    justify-content: space-between;
    padding: 10px;
    background-color: rgb(20, 20, 93);
    color: white;
}
nav ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 45%;
   
}
nav ul a{
    color: white;
    text-decoration: none;
    font-weight: bold;
}

nav ul a:hover
{
    color:lightsalmon;
}