*{
    margin: 0;
    padding: 0;
}
.home_title
{
    color:white;
    text-align: center;
    
}
.home
{
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 90px;
    height: 100%;
}
.home_container
{
    
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.home_formcontainer {
    box-shadow: 10px 10px 5px -4px rgba(158,177,179,0.75);
    background-color: rgb(68, 29, 210);
   border-radius: 10px;
   padding: 30px;
   width: 500px;
   
}
.home_formcontainer input
{
    border: 1px solid #e5e5e5;
    outline: none;
    padding: 15px 10px;
    width: 85%;
    border-radius: 10px;
    margin: 10px 0;
    background: transparent;
    color: white;
}
.home_formcontainer input::placeholder{
    color: white;
}
.home_formcontainer button{
    background: transparent;
    /* outline: none; */
    color: white;
    border: 1px solid white;
    border-radius: 10px;
    padding: 15px;
    margin-left: 150px;
    cursor: grab;
}
